/*----------------------------------------*/
/*  01 - Icon Box
/*----------------------------------------*/
/** Style 1 **/
.pbmit-ihbox-style-1 {
  text-align: center;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
}
.pbmit-ihbox-style-1:hover {
  transform: translateY(-10px);
}
.pbmit-ihbox-style-1 .pbmit-ihbox-icon-wrapper,
.pbmit-ihbox-style-1 .pbmit-ihbox-svg-wrapper {
  width: 115px;
  height: 115px;
  line-height: 115px;
  margin: 0 auto;
  margin-bottom: 30px;
  color: var(--pbmit-blackish-color);
  background-color: var(--pbmit-white-color);
  border-radius: 50%;
  box-shadow: 0px 2px 40px 0px rgb(0 0 0 / 5%);
}
.pbmit-ihbox-style-1 .pbmit-ihbox-icon-wrapper img {
  max-width: 65px;
}
.pbmit-ihbox-style-1 .pbmit-ihbox-svg-wrapper {
  font-size: 30px;
  fill: var(--pbmit-blackish-color);
}
.pbmit-ihbox-style-1 .pbmit-ihbox-icon-wrapper .pbmit-icon-type-icon {
  font-size: 60px;
}
.pbmit-ihbox-style-1 .pbmit-ihbox-icon-wrapper {
  font-size: 40px;
  font-weight: 600;
}
.pbmit-ihbox-style-1 .pbmit-element-title {
  font-size: 22px;
  line-height: 32px;
  margin-bottom: 8px;
  font-weight: 700;
}
.pbmit-ihbox-style-1 .pbmit-element-heading,
.pbmit-ihbox-style-1 .pbmit-element-subtitle {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 0;
}
.pbmit-ihbox-style-1 .pbmit-heading-desc {
  padding: 0 30px;
}
.pbmit-ihbox-style-1 .pbmit-ihbox-btn {
  margin-top: 20px;
}
.pbmit-ihbox-style-1 .pbmit-ihbox-btn a {
  color: var(--pbmit-white-color);
}
.pbmit-ihbox-style-1 .pbmit-ihbox-btn a:hover {
  color: var(--pbmit-global-color);
}
.pbmit-ihbox-style-1 .pbmit-ihbox-btn a:hover,
.pbmit-ihbox-style-1 .pbmit-ihbox-btn a:hover {
  color: var(--pbmit-blackish-color);
}
.pbmit-ihbox-style-1 .pbmit-ihbox-btn a:hover,
.pbmit-ihbox-style-1 .pbmit-ihbox-btn a:hover {
  color: var(--pbmit-white-color);
}
/** Style 2 **/
.pbmit-ihbox-style-2 .pbmit-ihbox-icon,
.pbmit-ihbox-style-2 .pbmit-ihbox-svg {
  margin-right: 30px;
}
.pbmit-ihbox-style-2 .pbmit-ihbox-icon-wrapper img {
  max-width: 90px;
  height: 90px;
  border-radius: 50%;
}
.pbmit-ihbox-style-2 .pbmit-heading-desc {
