}
/** Style 3 **/
.pbmit-ihbox-style-3 .pbmit-ihbox-svg,
.pbmit-ihbox-style-3 .pbmit-ihbox-icon {
  margin-bottom: 25px;
}
.pbmit-ihbox-style-3
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-text,
.pbmit-ihbox-style-3
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper
  .pbmit-icon-type-icon,
.pbmit-ihbox-style-3 .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-image img,
.pbmit-ihbox-style-3 .pbmit-ihbox-svg-wrapper {
  border-radius: 50%;
}
.pbmit-ihbox-style-3
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-text,
.pbmit-ihbox-style-3
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper
  .pbmit-icon-type-icon,
.pbmit-ihbox-style-3 .pbmit-ihbox-svg-wrapper {
  width: 90px;
  height: 90px;
  font-size: 50px;
  line-height: 100px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0px 5px 16px 0px rgba(33, 33, 91, 0.15);
  position: relative;
  z-index: 3;
  transition: all 0.25s ease-in-out;
}
.pbmit-ihbox-style-3
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-text::after,
.pbmit-ihbox-style-3
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper
  .pbmit-icon-type-icon::after,
.pbmit-ihbox-style-3 .pbmit-ihbox-svg-wrapper::after {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  content: "";
  background-color: var(--pbmit-global-color);
  transform: scale(0);
  transform-origin: center;
  transform-style: preserve-3d;
  border-radius: 50%;
  transition: all 0.4s cubic-bezier(0.62, 0.21, 0.45, 1.52);
  z-index: -1;
}
.pbmit-ihbox-style-3:hover
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-text::after,
.pbmit-ihbox-style-3:hover
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper
  .pbmit-icon-type-icon::after,
.pbmit-ihbox-style-3:hover .pbmit-ihbox-svg-wrapper::after {
  transform: scaleX(1);
}
.pbmit-ihbox-style-3:hover
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-text,
.pbmit-ihbox-style-3:hover
  .pbmit-ihbox-icon
  .pbmit-ihbox-icon-wrapper
  .pbmit-icon-type-icon {
  color: var(--pbmit-white-color);
}
.pbmit-ihbox-style-3 .pbmit-ihbox-icon-wrapper i {
  color: #fff;
}
.pbmit-ihbox-style-3:hover .pbmit-ihbox-svg-wrapper {
  fill: var(--pbmit-white-color);
}
.pbmit-ihbox-style-3 .pbmit-ihbox-icon-wrapper.pbmit-ihbox-icon-type-image img {
  max-width: 90px;
  height: 90px;
