/*----------------------------------------*/
/*  12 - Overlap Colomn
/*----------------------------------------*/
.overlap-colomn {
  position: relative;
}
.overlap-wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
}
.overlap-img,
.overlap-bg {
  position: absolute;
