/*----------------------------------------*/
/*  13 - Progress Bar
/*----------------------------------------*/
.progressbar {
  margin-bottom: 20px;
  overflow: hidden;
  padding-top: 10px;
}
.progressbar .progress-label {
  font-family: var(--pbmit-heading-typography-font-family);
  color: var(--pbmit-white-color);
  display: inline-block;
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
}
.progress {
  overflow: visible;
  border-radius: 5px !important;
  position: relative !important;
  height: 11px !important;
  padding: 1px;
  margin-top: 10px;
  background-color: var(--pbmit-white-color);
}
.progress .progress-bar {
  position: relative;
  overflow: visible;
  border-radius: 30px;
  background-color: var(--pbmit-global-color);
}
.progress.progress-percent-bg .progress-percent {
  line-height: 20px;
  position: absolute;
  right: -15px;
  top: -35px;
  font-size: 17px;
  color: var(--pbmit-white-color);
  font-family: var(--pbmit-heading-typography-font-family);
  font-weight: 700;
}
.progressbarone .progress-bar {
  background-color: var(--pbmit-blackish-color);
}
.pbmit-progress-style-1 .progress {
  border: 1px solid var(--pbmit-global-color);
  border-radius: 16px !important;
  height: 14px !important;
  padding: 1px;
  background-color: transparent !important;
}
.pbmit-progress-style-1 .progress .progress-bar {
  margin-bottom: 0;
}
.pbmit-progress-style-1 .progressbar .progress-label {
  font-family: var(--pbmit-heading-typography-font-family);
  color: var(--pbmit-heading-color);
  display: inline-block;
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
}
.pbmit-progress-style-1 .progress .progress-percent {
  line-height: 20px;
  position: absolute;
  right: -15px;
  top: -35px;
  font-size: 15px;
  color: var(--pbmit-blackish-color);
  font-family: var(--pbmit-heading-typography-font-family);
  font-weight: 600;
}
.progressbar.white .progress {
  background-color: var(--pbmit-light-color);
}

/*----------------------------------------*/
/*  14 - Accordion
/*----------------------------------------*/
.accordion {
  background: none;
  border-radius: 0;
}
.accordion-item {
  border: none;
  background-color: var(--pbmit-white-color);
  color: var(--pbmit-blackish-color);
  font-size: 18px;
  border-radius: 10px !important;
  margin-bottom: 15px;
  box-shadow: 0 2px 40px 0 rgb(0 0 0 / 5%);
}
.accordion-button {
  border: none;
  border-radius: 0 !important;
  box-shadow: none;
  padding: 25px 65px 25px 25px;
  background: none;
  outline: none;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.6px;
  color: var(--pbmit-heading-color);
}
.accordion-button::after {
  color: var(--pbmit-blackish-color);
  border-radius: 50%;
  background-image: none;
  content: "\e818";
  font-size: 24px;
  width: 0;
  height: 0;
  text-align: center;
  font-family: "pbminfotech-base-icons";
  transition: none;
  display: flex;
  align-items: center;
}
.accordion-button:not(.collapsed)::after {
  background-image: none;
  content: "\e86e";
  font-size: 18px;
  width: 0;
  height: 0;
  font-family: "pbminfotech-base-icons";
  transform: none;
  color: var(--pbmit-blackish-color);
}
.accordion-button:focus {
  box-shadow: none;
  border-color: #d4d4d4;
}
.accordion-button:not(.collapsed) {
  color: var(--pbmit-blackish-color);
  background-color: var(--pbmit-light-color);
  box-shadow: none;
  line-height: 20px;
}
.accordion-body {
  color: #787c8b;
  font-weight: 400;
  font-size: 16px;
  padding: 0 22px 1px;
}
.accordion-button.collapsed {
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
}
.accordion-item.collapsed {
  background-color: var(--pbmit-white-color);
}
.accordion-style-1 .accordion-button.collapsed {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
}
.accordion-style-1 .accordion-button:not(.collapsed) {
  color: var(--pbmit-blackish-color);
  background-color: var(--pbmit-white-color);
  box-shadow: none;
  line-height: 1px;
  border-radius: 10px !important;
  font-size: 16px;
  font-weight: 600;
}
.accordion-style-1 .accordion-body {
  padding: 6px 24px 5px;
}
.accordion-style-2 .accordion-button::after {
  position: absolute;
  margin-left: -26px;
}
.accordion-style-2 .accordion-button {
  padding-left: 50px;
}
.accordion-style-2 .accordion-button:not(.collapsed) {
  background-color: var(--pbmit-white-color);
  border-radius: 10px !important;
}
.accordion-style-3 .accordion-item {
  border: none;
  background-color: var(--pbmit-light-color);
  color: var(--pbmit-blackish-color);
  font-size: 18px;
  border-radius: 0px !important;
  margin-bottom: 15px;
  box-shadow: none;
}
.accordion-style-3 .accordion-button {
  padding: 15px 25px;
  color: var(--pbmit-white-color);
}
.accordion-style-3 .accordion-button::after {
  padding: 18px 25px 18px 12px;
  border-radius: 0;
  color: var(--pbmit-white-color);
  background-color: var(--pbmit-blackish-color);
}
.accordion-style-3 .accordion-item.active .accordion-body {
  background-color: var(--pbmit-blackish-color);
}
.accordion-style-3 .accordion-body {
  padding: 15px 22px;
}
.accordion-style-3 .accordion-item.active .accordion-button::after {
  content: "\e86e";
  font-size: 16px;
}

/*----------------------------------------*/
/*  15 - Tab
/*----------------------------------------*/
.pbmit-tab-style-1 {
