/*----------------------------------------*/
/** Style 1 **/
.pbmit-static-box-shap .container {
  position: relative;
  z-index: 1;
}
.pbmit-static-box-style-1 .pbminfotech-box-title h4,
.pbmit-static-box-style-1 .pbminfotech-static-box-desc,
