.pbmit-grid-item {
  display: grid;
  margin-bottom: 0;
  padding: 0;
  width: auto !important;
}
.pbmit-card {
  box-shadow: 0 15px 38px 0 rgb(0 0 0 / 10%) !important;
  border-radius: 5px !important;
  border: 0;
  margin-bottom: 20px;
}
.pbmit-card-body {
  padding: 10px 20px 20px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  order: 2;
}
.pbmit-grid-item .pbmit-card-media img {
  border-radius: 0;
  box-shadow: none;
  display: block;
  height: auto;
  margin: 0;
  object-fit: cover;
  order: 1;
  width: 100%;
  transform: scale(1);
  -webkit-transition: all 0.6s ease-in-out;
}
.pbmit-media-card-inner {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
}
.pbmit-grid-item .pbmit-media-card-inner:after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  content: "";
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  -webkit-transition: opacity 0.4s ease-in-out;
  -o-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out;
}
.pbmit-grid-item:hover .pbmit-card-media .pbmit-media-card-inner:after {
  opacity: 1;
}
.pbmit-grid-item:hover .pbmit-card-media img {
  transform: scale(1.03) rotate(0.5deg);
}
.pbmit-card-title {
  font-size: 24px;
  line-height: 34px;
  padding-bottom: 10px;
}
.pbmit-card-text {
  display: none;
}
.pbmit-progress-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 15px 0;
}
.pbmit-card-progress .pbmit-info {
  display: flex;
  flex-direction: column;
  position: relative;
}
.pbmit-label {
  font-weight: 500;
  color: var(--pbmit-blackish-color);
}
.goal-text {
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  color: var(--pbmit-global-color);
}
.pbmit-card-progress strong {
  display: none;
}
.pbmit_info {
  display: flex;
  flex-direction: column;
  position: relative;
}
.pbmit-card-progress .progress {
  border-bottom: none;
  background-color: #eee !important;
}
.pbmit-card-progress .progress .progress-bar {
  height: 100%;
  border-radius: 20px;
}
button.pbmit-btn.pbmit-btn- {
  display: none;
}
.pbmit-card-media {
  padding: 20px;
}
.donations-single-img {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
}
.donations-single-img::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  left: 0;
  background: linear-gradient(
    0deg,
    rgba(8, 24, 57, 0.6) 0%,
    rgba(0, 50, 129, 0) 90%
  );
  opacity: 0;
  transform-origin: right center 0;
  transform: perspective(100px) rotateY(-120deg);
  transform-style: flat;
  transition: transform 0.5s ease-in-out, opacity 0.2s ease-in-out;
}
.donations-single-img:hover:before {
  opacity: 0.9;
  transform: rotateY(0deg);
}
.donations-single-details .pbmit-goal-progress {
  margin-top: 15px;
}
.donations-single-details .progress {
  border-bottom: none;
}
.single-progress-status {
  display: flex;
  align-items: center;
  margin-top: 20px;
}
.single-progress-status strong {
  padding-left: 15px;
  margin-right: 15px;
  font-weight: 500;
  color: var(--pbmit-blackish-color);
}
.pbmit-notice {
  position: relative;
  padding: 1em 1.5em 1em 2em;
  border-left: 4px solid;
  margin: 30px 0;
  background: #f3f3f3;
  box-shadow: 0 1px 1px 0 rgb(0 0 0 / 10%);
  border-color: #ffba00;
}
i.fa.fas.fa-exclamation-triangle {
  background-color: #ffba00;
  content: "";
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  position: absolute;
  top: 50%;
  font-size: 16px;
  margin-top: -15px;
  left: -17px;
  color: #fff;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  text-align: center;
  line-height: 30px;
  text-shadow: 1px 1px rgb(0 0 0 / 50%);
}
.pbmit-notice p {
  margin: 0 !important;
  color: #333;
}
.donations-single-details li {
  list-style-type: none;
  list-style-image: none;
  margin: 0 5px 5px 0;
}

.page-content {
